.Sidebar{
    flex: 1;
    height: calc(100vh - 50px);
    background-color: rgb(247, 242, 242);
    position: sticky;
    top: 50px;
}
.SidebarWrapper{
    padding: 20px;
    color: #545455;
}
.SidebarMenu{
    margin-bottom: 10px;
}
.SidebarTitle{
    font-size: 15px;
    color: lightslategray;
}
.SidebarList{
    list-style: none;
    padding: 5px;
}
.SidebarListItem{
    padding: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 6px;
}
.SidebarListItem.active,
.SidebarListItem:hover{
    background-color: rgb(228, 228, 250);
}
.Icon{
    margin-right: 5px;
    font-size: 20px !important;
}
.link{
    text-decoration: none;
    color: inherit;
}