.Featured {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.FeaturedItem {
  flex: 1;
  margin: 20px 20px;
  padding: 30px;
  border-radius: 10px;
  cursor: pointer;
  -webkit-box-shadow: 0px 10px 15px -5px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 10px 15px -5px rgba(0, 0, 0, 0.75);
  background-color: rgb(221, 248, 248);
}
.FeaturedTitle {
  font-size: 20px;
}
.FeaturedMoneyContent {
  margin: 10px 0;
  display: flex;
  align-items: center;
}
.FeaturedMoney {
  font-size: 30px;
  font-weight: 600;
}
.FeaturedRate {
  display: flex;
  align-items: center;
  margin-left: 20px;
}
.FeaturedSub {
  font-size: 15px;
  margin-left: 5px;
  color: gray;
}
.FeaturedIcons {
  font-size: 14px;
  margin-left: 5px;
  color: green;
}
.FeaturedIcons.negative {
  color: red;
}