.UserList {
  flex: 4;
  padding: 20px;
}
.User {
  display: flex;
  align-items: center;
}
.UserProfile {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}
.UserEdit {
  border: none;
  border-radius: 10px;
  padding: 5px 10px;
  background-color: #3bb0ff;
  color: white;
  cursor: pointer;
  margin-right: 20px;
}
.UserDelt {
    color: red;
    cursor: pointer;
}
