.NewUser {
  flex: 4;
  margin: 20px;
}
.NewUserForm {
  display: flex;
  flex-wrap: wrap;
}
.NewUserItem {
  width: 400px;
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  margin-right: 20px;
}
.NewUserItem > label {
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: 600;
  color: rgb(194, 194, 194);
}
.NewUserItem > input {
  height: 10px;
  padding: 10px;
  border: 1px solid gray;
  border-radius: 5px;
}
.NewUserGender>input {
    margin-top: 15px;
}
.NewUserGender>label{
    margin: 10px;
    font-size: 18px;
    color: #554444;
}
.NewUserSelect{
    height: 40px;
    border-radius: 5px;
}
.NewUserBtn{
    width: 200px;
    border: none;
    background-color: darkblue;
    color: white;
    padding: 7px 10px;
    font-weight: 600;
    border-radius: 10px;
    margin-top: 30px;
    cursor: pointer;
}
