.ProductList {
  flex: 4;
  margin: 20px;
}
.ProductListImg {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}
.ProductListItem{
    display: flex;
    align-items: center;
}
.ProductListEdit {
  border: none;
  border-radius: 10px;
  padding: 5px 10px;
  background-color: #3bb0ff;
  color: white;
  cursor: pointer;
  margin-right: 20px;
}
.ProductListDelete {
  color: red;
  cursor: pointer;
}
