.Product {
  flex: 4;
  margin: 20px;
}
.ProductTitleContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.ProductAddBtn {
  width: 140px;
  border: none;
  padding: 5px;
  background-color: teal;
  color: #fff;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
}
.ProductTop {
  display: flex;
}
.ProductTopLeft,
.ProductTopRight {
  flex: 1;
}
.ProductTopRight {
  padding: 20px;
  margin: 20px;
  -webkit-box-shadow: 0px 10px 15px -5px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 10px 15px -5px rgba(0, 0, 0, 0.75);
}
.ProductInfoImg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 20px;
}
.ProductName {
  font-weight: 600;
}
.ProductInfoTop {
  display: flex;
  align-items: center;
}
.ProductInfoBottom {
  margin-top: 10px;
}
.ProductInfoItem {
  width: 150px;
  display: flex;
  justify-content: space-between;
}
.ProductInfoValue {
  font-weight: 300;
}
.ProductBottom {
  padding: 20px;
  margin: 20px;
  -webkit-box-shadow: 0px 10px 15px -5px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 10px 15px -5px rgba(0, 0, 0, 0.75);
}
.ProductForm{
    display: flex;
    justify-content: space-between;
}
.ProductFormLeft{
    display: flex;
    flex-direction: column;
}
.ProductFormLeft>label{
    margin-bottom: 10px;
    color: gray;
}
.ProductFormLeft>input{
    margin-bottom: 10px;
    border: none;
    padding: 5px;
    border-bottom: 1px solid gray;
}
.ProductFormLeft>select{
    margin-bottom: 10px;
}
.ProductUploadImg{
    width: 150px;
    height: 150px;
    border-radius: 10px;
    object-fit: cover;
    margin-right: 20px;
}
.ProductFormRight{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.ProductUpload{
    display: flex;
    align-items: center;
}
.ProductBtn{
    border: none;
    padding: 5px;
    border-radius: 5px;
    background-color: blue;
    color: #fff;
    font-weight: 600;
    cursor: pointer;
}
