.Widget {
  flex: 1;
  -webkit-box-shadow: 0px 10px 15px -5px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 10px 15px -5px rgba(0, 0, 0, 0.75);
  padding: 20px;
  margin-right: 20px;
  border-radius: 5px;
  background-color: rgb(243, 247, 250);
}
.WidgetTitle {
  font-size: 26px;
  font-weight: 600;
}
.WidgetList {
  margin: 0;
  padding: 0;
  list-style: none;
}
.WidgetListItem {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 20px 0px;
}
.WidgetImg {
  width: 40px;
  height: 40px;
  border-radius: 20%;
  object-fit: cover;
}
.WidgetUser {
  display: flex;
  flex-direction: column;
}
.WidgetUserName {
  font-weight: 600;
}
.WidgetUserTitle {
  font-weight: 300;
}
.WidgetButton {
  display: flex;
  align-items: center;
  border: none;
  border-radius: 7px;
  padding: 7px 10px;
  background-color: #f7f3bd;
  color: #555;
  cursor: pointer;
}
.WidgetIcon {
  font-size: 16px !important;
  margin-right: 5px;
}
