.NewProduct {
  flex: 4;
  margin: 20px;
}
.AddProductForm {
  margin-top: 10px;
}
.AddProductItem {
  width: 250px;
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}
.AddProductItem > label {
  color: gray;
  font-weight: 600;
  margin-bottom: 10px;
}
.AddProductItem > input {
  padding: 10px;
}
.AddProductItem > select {
  padding: 10px;
}
.AddProductBtn {
  margin-top: 10px;
  padding: 7px 10px;
  border: none;
  border-radius: 10px;
  background-color: blue;
  color: white;
  font-weight: 600;
  cursor: pointer;
}
