.WidGetLg {
  flex: 2;
  -webkit-box-shadow: 0px 10px 15px -5px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 10px 15px -5px rgba(0, 0, 0, 0.75);
  padding: 20px;
  border-radius: 5px;
  background-color: rgb(243, 247, 250);
}
.WidGetLgTitle{
  font-size: 22px;
  font-weight: 600;
}
.WidGetLgTable{
  width: 100%;
  border-spacing: 20px;
}
.WidGetLgHeader{
  text-align: left;
}
.WidGetLgImg{
  width: 40px;
  height: 40px;
  border-radius: 20%;
  object-fit: cover;
  margin-right: 10px;
}
.widgetLgButton{
  padding: 5px 7px;
  border: none;
  border-radius: 5px;
}
.widgetLgButton.Approved{
  background-color: #e5faf2;
  color: #3bb077;
}
.widgetLgButton.Declined{
  background-color: #fff0f1;
  color: #d95087;  
}
.widgetLgButton.Pending{
  background-color: #ebf1fe;
  color: #2a7ade;
}
.WidGetLgUser{
  display: flex;
  align-items: center;
  font-weight: 2600px;
}
.WidGetLgDate,
.WidGetLgAmount{
  font-weight: 300;
}
